//Global

a {color: $color-text;text-decoration: none;-webkit-font-smoothing: antialiased;
  @include transition(color .3s ease);
  &:hover {color: $color-brand;text-decoration: none;}
}

img {max-width: 100%}



//White space
.white_space_nowarp {white-space: nowrap}


.font_italic {font-style: italic}
.font_title {font-family: $font-title}

.center_block {
  @include center-block(100%);
}


.scroll_mobile_h {
  display: flex !important;; /* [1] */
  flex-wrap: nowrap !important; /* [1] */
  overflow-x: auto !important;; /* [1] */
  -webkit-overflow-scrolling: touch; /* [4] */
  -ms-overflow-style: none; /* [5] */
  overflow: -moz-scrollbars-none;
}
.scroll_mobile_h::-webkit-scrollbar {
  //width: 0 !important
}

.fix_top {position: sticky;top: 0;z-index: 1}
